header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.leveler {
    margin-top: -5%;
}

/* ===================== CTA ================== */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.btn-primary{
    transition: var(--transition);
}

.btn-primary:hover {
    font-style: italic;
}

/* ===================== Header Socials ================== */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: -1rem;
}

.header__socials a:hover {
    transform: rotate(30deg)
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ===================== ME ================== */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}


/* ===================== Scroll Down ================== */

.scroll__down {
    position: absolute;
    right: -2.5rem;
    bottom: 1rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

.scroll__down:hover {
    font-style: italic;
}

/* =================== MEDIA Queries (Medium Devices) ===================== */

@media screen and (max-width: 1024px) { 
        header {
            height: 68vh;
        }
}


/* =================== MEDIA Queries (Small Devices) ===================== */

@media screen and (max-width: 600px) { 

    header {
        height: 100vh;
        margin-top: -65px;
    }
    
    .header__socials,
    .scroll__down {
        display: none;
    }
}